<template>
    <div style="padding: 30px 0;">
   <div class="d-tabs">
       <div class="d-tabs-head">
           <div class="tab-nav"> 
               <div class="tab-nav-item" :class="{'active':tabCur==0}" @click="tabClick(0)">usdt充值</div>
               <div class="tab-nav-item" :class="{'active':tabCur==1}" @click="tabClick(1)">银行卡转账</div>
            </div>
        </div> 
        <div class="d-tabs-content">
            <div class="tab-pane" label="usdt充值" v-if="tabCur==0">
                
                <div style="margin: 120px auto 0;text-align: center;">暂无可用渠道</div>
                 
            </div>
            <div class="tab-pane" label="银行卡转账" v-if="tabCur==1">
                <div style="font-size: 30px;text-align: center;padding: 40px 0 20px;color: #828282;border-bottom: 1px solid #ccc;">当前账户余额：<span style="font-size: 40px;color: #000;">{{userInfo.amount}}元</span></div>
                

                <div style="color:#000;font-size: 16px;padding-left: 40px;margin: 20px auto;">请选择充值金额：</div>
                <ul style="margin: 30px;font-size:16px;" class="jine-ul">
                    <li :class="curIndex==index?'tative':''" v-for="(item,index) in cuIconList" :key="index" @click="choiceNum(index)">
                        <span v-if="index == 5">
                            <input :placeholder="item.text" v-model="amount" style="border: none;background:none;text-align:center;display: block;height: 100%;"/>
                        </span>
                        <span v-else>
                            {{item.text}}
                        </span>
                    </li>
                </ul>

                <div style="color:#000;font-size: 16px;padding-left: 40px;margin: 20px auto 0;">收款账户：</div>

                <div class="search-form" style="flex-direction: column;">
                    <div class="form-item">
                        <span>收款银行：</span>
                        <el-select v-model="curBank" placeholder="请选择" style="width: 300px;" @change="selectChange">
                            <el-option
                            v-for="item in bankList"
                            :key="item.id"
                            :label="item.bank_name"
                            :value="item.id">
                            </el-option>
                        </el-select>
                    </div>

                    <div class="form-item">
                        <span>收款户主：</span>
                        <el-input type="text" style="width: 300px;" v-model="curBankData_realName" readonly></el-input>
                    </div>
                     <div class="form-item">
                        <span>收款账号：</span>
                        <el-input type="text" style="width: 300px;" v-model="curBankData_bankno" readonly></el-input>
                    </div>
                     <div class="form-item">
                        <span>收款支行：</span>
                        <el-input type="text" style="width: 300px;" v-model="curBankData_branch" readonly></el-input>
                    </div>
                     <div class="form-item">
                        <span>转账姓名：</span>
                        <el-input type="text" style="width: 300px;" v-model="real_name"></el-input>
                    </div>
                     <div class="form-item">
                        <span>收款备注：</span>
                        <el-input type="text" style="width: 300px;" v-model="remark"></el-input>
                    </div>

                    <div class="form-item" style="text-align: center;">
                         <el-button type="primary" style="width: 200px;margin: 0 auto;" @click="submit">提交</el-button>
                    </div>

                </div>
            </div>
            
        </div>

      

    </div>

    
    </div>
</template>

<script>
import { bankCollectList,czUsers } from "../api/public";
export default {
  data() {
    return {
        amount: '',
        cuIconList: [{
            id: 0,
            text: '100',
        }, {
            id: 1,
            text: '200',
        }, {
            id: 2,
            text: '300',
        }, {
            id: 3,
            text: '400',
        }, 
        {
            id: 4,
            text: '500',
        },
        {
            id: 5,
            text: '请输入金额',
        }],
        curIndex: 0,

        tabCur: 1,
        yhhdList: [],
        curPage: 1,
        total: 1,
        limit: 10,
        tabLi: 0,

        bank: '',
        options: [],
        userInfo: {},
        real_name: '',
        remark: '',
        bankList: [{
            real_name: '',
            bank_no: '',
            branch: ''
        }],
        curBank: 1,
        curBankData_realName: '',
        curBankData_bankno: '',
        curBankData_branch: '',
    };
  },
  
   watch: {
    $route: "fetchData"
  },
  mounted() {
      this.userInfo = JSON.parse(localStorage.getItem('user_info'))
      console.log(this.$route.query)
      this.tabCur = this.$route.query.id || 1
      this.bankCollectList()
  },
  methods: {
    selectChange(e) {
        console.log(e)
        console.log(this.curBank)
        this.curBankData_realName = this.bankList[this.curBank-1].real_name
        this.curBankData_bankno = this.bankList[this.curBank-1].bank_no
        this.curBankData_branch = this.bankList[this.curBank-1].branch
    },
    choiceNum(index) {
        this.curIndex = index
    },
    submit() {
        if (!this.real_name) {
            this.$message.error("请填写转账姓名");
            return false
        }
        czUsers({
            "id": this.userInfo.id,
            "amount": this.curIndex == 5 ? +this.amount : +this.cuIconList[this.curIndex].text,
            "bank_type": "网银",
            "bank_order_no": '', // 订单流水号
            "remark": this.remark, // 备注
            "real_name": this.real_name,
            "bank_no": this.curBankData_bankno,
            "bank_name": this.bankList[this.curBank-1].bank_name,
            "branch": this.curBankData_branch,
            "bank_id": this.bankList[this.curBank-1].id
        }).then(res => {
            if (res.code == 1) {
                this.curBank = 1,
                this.curBankData_realName = this.bankList[this.curBank].real_name
                this.curBankData_bankno = this.bankList[this.curBank].bank_no
                this.curBankData_branch = this.bankList[this.curBank].branch
                this.remark = ''
                this.real_name = ''
                this.$message.success('提交成功')
               
            } else {
                this.$message.error(res.msg);
            }
        })
    },
    fetchData(newVal, oldVa) {
        console.log(oldVa)//oldVa 上一次url
        console.log(newVal);//newVal 这一次的url
        this.tabCur = newVal.query.id
        
    },
    tabClick(i) {
        this.tabCur = i
       
    },
    bankCollectList() {
        bankCollectList({
            "page": 1,
            "limit": 100
        }).then(res => {            
            if (res.code == 1) {
                this.bankList = res.data
                console.log(this.bankList[this.curBank-1])
                // this.curBankData = this.bankList[this.curBank]
                this.curBankData_realName = this.bankList[this.curBank].real_name
                this.curBankData_bankno = this.bankList[this.curBank].bank_no
                this.curBankData_branch = this.bankList[this.curBank].branch
            }
        })
    },
    
  }
}
</script>

<style lang="scss">
.jine-ul{
    li{
        display: inline-block;
        width: 169px;
        height: 80px;
        line-height: 80px;
        text-align: center;
        background-color: #eee;
        margin: 10px;
        cursor: pointer;
        input{
            font-size: 16px;
            width: 100%;
            padding: 0 20px;
            box-sizing: border-box;
        }
        &.tative{
            background-color: #282d4f;
            color:#fff;
            input{
                color: #fff;
            }
        }
    }
}
.d-tabs {
    width: 1200px;
    min-height: 630px;
    margin: 0 auto;
    background: #fff;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12),0 0 6px 0 rgba(0,0,0,.04);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    position: relative
}

.d-tabs .d-tabs-head {
    overflow: hidden;
    height: 40px
}

.d-tabs .inline-item {
    background: #282d4f;
    width: 100%;
    font-size: 0
}

.d-tabs .inline-item .tab-nav-item {
    letter-spacing: 1px;
    display: inline-block;
    padding: 0 10px;
    color: #fff;
    font-size: 14px;
    height: 26px;
    line-height: 26px;
    transition: all .3s;
    margin: 7px 10px;
    position: relative;
    border-radius: 4px
}

.d-tabs .inline-item .tab-nav-item:hover {
    color: #ec002c;
    text-decoration: underline;
    cursor: pointer
}

.d-tabs .inline-item .active {
    background-image: linear-gradient(-180deg,#d4d4d4,#fff);
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.5);
    background: #fff;
    color: #282d4f
}

.d-tabs .inline-item .active:hover {
    color: #282d4f;
    text-decoration: none
}

.d-tabs .tab-nav {
    background: #282d4f;
    width: 100%;
    font-size: 0
}

.d-tabs .tab-nav .tab-nav-item {
    letter-spacing: 1px;
    display: inline-block;
    padding: 0 32px;
    color: #fff;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    font-weight: 700;
    transition: all .3s;
    position: relative
}

.d-tabs .tab-nav .tab-nav-item:hover {
    background-color: #7e5acf;
    cursor: pointer
}

.d-tabs .tab-nav .active {
    background: #fff;
    color: #282d4f
}

.d-tabs .tab-nav .active:hover {
    background-color: #fff;
    color: #282d4f;
    cursor: inherit
}

.d-tabs .both-side {
    background: #282d4f;
    width: 100%;
    font-size: 0
}

.d-tabs .both-side .tab-nav-item {
    letter-spacing: 1px;
    display: inline-block;
    position: relative;
    margin: 0 32px;
    color: #ec002c;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    font-weight: 700
}

.d-tabs .both-side .tab-nav-item:before {
    content: "";
    height: 4px;
    width: 4px;
    background: #ec002c;
    position: absolute;
    left: -10px;
    top: 17px
}

.d-tabs .both-side .tab-nav-item:after {
    content: "";
    height: 4px;
    width: 4px;
    background: #ec002c;
    position: absolute;
    right: -10px;
    top: 17px
}

.d-tabs .show-tab {
    position: absolute;
    width: 100%;
    top: 0;
    z-index: 1
}

.d-tabs .show-tab .tav-rate {
    visibility: visible;
    opacity: 1
}
</style>